header {
  background: #6dc5ff20;
  background: url("../../../../img/bg/header-bg-small.jpg") bottom right no-repeat;
  background-size: 100% 100%;
  box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);

}
.header-top-line {
  width: 100%;
  height: .8rem;
  background: #6dc5ff;
}
.header {
  position: relative;
  display: flex;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  align-items: center;
  column-gap: 1rem;
}
.header-img {
  position: absolute;
  top: 5px;
  right: 0;
  max-width: 380px;
  img {
    max-width: 100%;
    height: 100%;
  }
}

.header-info {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  z-index: 2;
}
.header-email-btn, .header-order-btn {
  font-size: .8rem;
  text-align: center;
  min-width: 200px;
}
.header-email-btn:before {
  content: '';
  display: inline-block;
  margin-right: .5rem;
  width: 1rem;
  height: 1rem;
  vertical-align: text-bottom;
  background: url("../../../../img/svg/email.svg") center no-repeat;
}
.header-order-btn:before {
  content: '';
  display: inline-block;
  margin-right: .5rem;
  width: 1rem;
  height: 1rem;
  vertical-align: text-bottom;
  background: url("../../../../img/svg/pen.svg") center no-repeat;
}
