@media (max-width: 1270px) {
  .header {
    justify-content: space-between;
  }
  .header-img {
    position: static;
    height: 113px;
  }
  .header-logo-left {
    img {
      max-width: 400px;
    }
  }
}

@media (max-width: 1200px) {
  .content-big-img {
    max-width: 360px;
  }
  .modal-body {
    font-size: .6rem;
  }
  .chars-list {
    li {
      padding: 1px 0;
    }
  }
}

@media (max-width: 992px) {
  html {
    font-size: 18px;
  }
  .header-img {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 113px;
  }
  .header {
    justify-content: flex-start;
  }
  .header-logo-left {
    img {
      max-width: 300px;
    }
  }
  #go-top {
      bottom: 100px;
  }

  .modal-body {
    flex-direction: column;
    column-gap: 0;
    row-gap: 1rem;
  }
  .tinymodal-window {
    width: 400px;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 16px;
  }
  .header-img {
    display: none;
  }
  .header {
    justify-content: space-between;
  }
  .content-item {
    padding: 1rem;
  }
  .content-big-img {
    max-width: 200px;
  }
  #go-top {
    right: 30px;
    width: 35px;
    height: 35px;
  }

}

@media (max-width: 576px) {
  html {
    font-size: 14px;
  }
  .header {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .header-logo-left {
    img {
      max-width: 200px;
    }
  }
  .header-email-btn, .header-order-btn {
    min-width: 100px;
  }
  .content-item {
    flex-direction: column;
    box-shadow: 5px 5px 5px 0 rgba(34, 60, 80, 0.6);
    margin: 0 10px;
  }
  .content-item-image {
    margin-top: 10px;
    margin-right: 10px;
    align-self: flex-end;
  }
  .content-item-text {
    align-self: stretch;
  }
  .content-item-title {
    font-size: 1.5rem;
  }
  .content-item-footer {
    align-self: center;
  }
  .order-title {
    font-size: 1.5rem;
  }
  .footer-wrap {
    img {
      height: 45px;
    }
  }
  .chars-list {
    li {
      padding: 1px 0;
    }
  }
  .tinymodal-window {
    width: 300px;
  }

  .modal-wrapper {
    padding: .5rem;
  }
  .modal-close {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 20px;
    height: 20px;
  }
  .modal-body-img {
    max-width: 100%;
    height: 120px;
  }
}

@media (max-width: 470px) {
  #form {
    max-width: 100%;
  }

  .modal-header {
    justify-content: flex-start;
    padding-left: 1rem;
  }
  .tinymodal-window {
    width: 250px;
  }
}

@media (max-width: 430px) {
  .header-logo-left {
    img {
      max-width: 150px;
    }
  }
  .header {
    column-gap: 5px;
  }
  .footer-wrap {
    p {
      text-align: center;
    }
  }
  #go-top {
    bottom: 80px;
  }
}

@media (max-width: 350px) {
  .header-logo-left {
    img {
      max-width: 100px;
    }
  }
}


